<template>
	<div id="single-blog">
		<div id="banner">
   <img src="../assets/img/banner1.jpg"> 
  </div>
  <div class="content">
    <section class="content-main-left">
		<h1>{{blog.title}}</h1>
		 <p class="article-times">
          <small>阅读1</small>
          <small>作者：{{blog.author}}</small>
          <small>9月10日</small>
          <span v-for="i in blog.categories">{{i}}</span>
        </p>
		<article>{{blog.content}}</article>
		




	</section>
		</div>
	</div>
</template>
<script >
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get('https://blog-demo-5478d.firebaseio.com/posts/'+this.id+".json")
			.then(function(data) {
				return data.json();
				// console.log("nice");
				// this.blog=data.body;
			})
			.then(function(data){
				this.blog=data;
			})
		}
	}
</script>
<style scoped>
	.article-times{
		width: 300px;
		height: 22px;
		float: right;
		margin-top: 0;
		margin-bottom: 2%;

	}
	.content-main-left h1{
		margin-bottom: 2%;
	}
	.content-main-left article{
	clear: both;
	overflow: hidden;
	margin-top: 30px;
	margin: 20px auto;
    padding-top: 5px;
    line-height: 26px;
    letter-spacing: 1px;
	}
	.content-main-left .article-content{
    
	}
	.article-times span {
		margin-left: 2%;
		color: #fff;
		background-color: #666;
	}
</style>


